.randomchar{display:grid;grid-template-columns:repeat(2, 50%);box-shadow:5px 5px 20px 0px rgba(0,0,0,.25)}.randomchar__block{display:grid;grid-template-columns:180px auto;padding:40px 35px;column-gap:30px}.randomchar__img{width:180px;height:180px;object-fit:cover}.randomchar__img-not-available{width:180px;height:180px;object-fit:contain}.randomchar__info{display:grid;grid-template-rows:29px 90px 38px;row-gap:10px}.randomchar__name{font-size:22px;font-weight:bold;line-height:29px;text-transform:uppercase}.randomchar__descr{font-size:14px;line-height:18px}.randomchar__btns a:nth-child(1){margin-right:30px}.randomchar__static{padding:40px 35px;background-color:#232222;position:relative}.randomchar__static button{margin-top:13px}.randomchar__title{color:#fff;font-size:24px;font-weight:700;line-height:32px}.randomchar__title:nth-child(2){margin-top:33px}.randomchar__decoration{position:absolute;bottom:10px;right:-36px}.fade-selectedСhar-enter{opacity:0;transform:scale(0.5, 0.5)}.fade-selectedСhar-enter-active{opacity:1;transform:scale(1, 1)}.fade-selectedСhar-exit{opacity:1;transform:scale(1, 1)}.fade-selectedСhar-exit-active{opacity:0;transform:scale(0.5, 0.5)}.fade-selectedСhar-enter-active,.fade-selectedСhar-exit-active{transition:all 300ms ease-in-out}